import * as React from 'react';
import {
    Month,
    TimelineMonth,
    ScheduleComponent,
    ViewsDirective,
    ViewDirective,
    ResourcesDirective,
    ResourceDirective,
    Inject,
    Resize,
    DragAndDrop,
    Day, Week, WorkWeek, Agenda
} from '@syncfusion/ej2-react-schedule';
import { extend } from '@syncfusion/ej2-base';
import {resourceData, timelineResourceData} from './data/eventData';
/**
 * schedule multiple dragging sample
 */
function App() {
    const data = extend([], resourceData.concat(timelineResourceData), null, true);
    const ownerData = [
        { text: 'Nancy', id: 1, color: '#df5286' },
        { text: 'Steven', id: 2, color: '#7fa900' },
        { text: 'Robert', id: 3, color: '#ea7a57' },
    ];
    return (<div className='schedule-control-section'>
        <div className='col-lg-12 control-section'>
            <div className='control-wrapper'>
                <ScheduleComponent width='100%' height='650px' selectedDate={new Date(2023, 0, 4)} currentView='Month' allowMultiDrag={true} allowResizing={false} showQuickInfo={false} eventSettings={{ dataSource: data }} group={{ resources: ['Owners'] }}>
                    <ResourcesDirective>
                        <ResourceDirective field='TaskId' title='Owners' name='Owners' dataSource={ownerData} textField='text' idField='id' colorField='color'>
                        </ResourceDirective>
                    </ResourcesDirective>
                    <ViewsDirective>
                        <ViewDirective option='Month'/>
                        <ViewDirective option='TimelineMonth'/>
                        <ViewDirective option='Day'/>
                    </ViewsDirective>
                    <Inject services={[Day, Week, WorkWeek, Month, Agenda, Resize, DragAndDrop]} />
                </ScheduleComponent>
            </div>
        </div>
    </div>);
}
export default App;